import React, { useEffect } from "react";

import Navbar from "../../../components/genral_components/Navbar/Navbar";

import SubCategoriesBanner from "../../../templates/subcategories/subcategoriesbanner/SubCategoriesBanner";

import SubCategoriesCardLeft from "../../../templates/subcategories/sub-categories-card-left/SubCategoriesCardLeft";

import SubCategoriesCardRight from "../../../templates/subcategories/sub-categories-card-right/SubCategoriesCardRight";

import Pricing from "../../../components/services_page_components/pricing/Pricing";

import Skills from "../../../components/home_page_components/ourskills/Skills";

import Ourwork from "../../../components/home_page_components/ourwork/Ourwork";

import Footer from "../../../components/genral_components/Footer/Footer";

import pic1 from "./pic1.png"
import pic2 from "./pic2.png"
import pic3 from "./pic3.png"
import pic4 from "./pic4.png"

import "./SocialMediaAdvertising.css";

import socialMediaAdvertisementBanner from "./SocialMediaAdvertising.jpg";

import { useLocation } from "react-router-dom";

export default function SocialMediaAdvertising() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  
  return (
    <div className="social-media-advertising-main-container">
      <Navbar />

      <SubCategoriesBanner
        bannerHeading="Social Media Advertising"
        bannerParagraph="supercharge your online presence and drive business growth through highly targeted social media advertising!"
        bannerButton1="LEARN MORE"
        bannerButton2="OUR WORK"
        bannerBackgroundImage={socialMediaAdvertisementBanner}
      />

      <SubCategoriesCardLeft
        heading="Management of advertising campaigns"
        paragrapgh=" Social media platforms offer tools and dashboards to efficiently manage advertising campaigns. Advertisers have the ability to set budgets for paid social media marketing services, specify campaign goals, Ad placements, track performance indicators, and make real-time adjustments."
        button="LET'S CHAT"
        picture={pic1}
      />

      <SubCategoriesCardRight
        heading="Analytics and monitoring"
        paragraph="Social media advertising systems include advanced analytics and tracking features. Key metrics including impressions, reach, clicks, conversions, engagement, and more may be monitored by advertisers. By using this information, campaigns' efficacy can be assessed and data-driven optimizations can be made."
        button="LET'S CHAT"
        picture={pic2}
      />

      <SubCategoriesCardLeft
        heading="Remarketing"
        paragrapgh="Companies can run remarketing campaigns using social media advertising. Users who have already interacted with their website, app, or social media profiles are the target audience for this advertising. Remarketing aids in re-engaging potential clients and boosting conversion rates."
        button="LET'S CHAT"
        picture={pic3}
      />

      <SubCategoriesCardRight
        heading="Ad performance optimization"
        paragraph="Success in social media advertising depends on ongoing analysis and improvement. In order to increase ad performance over time, advertisers can experiment with various ad variations, test various targeting options, alter budgets, and fine-tune campaign strategies."
        button="LET'S CHAT"
        picture={pic4}
      />

      <p className="social-media-advertising-paragraph">
        It's significant to remember that every social media site has unique
        rules and regulations surrounding advertising. To ensure compliance and
        prevent any problems with their advertising, advertisers should become
        familiar with these rules.
      </p>

      <br/>
      <br/>
      <br/>
      <br/>

      <Pricing/>

      <br/>
      <br/>
      <br/>

      <Skills/>

      <br/>
      <br/>
      <br/>

      <Ourwork/>

      <Footer/>

    </div>
  );
}
